.alert p{
  margin: 0 30px 0 0;
  padding: 0;
  font-size: 20px;
}
.alert{
  top: 56px;
  left: 56px;
  position: fixed;
  width: calc(100% - 56px - 20px);
  transform: translateY(calc(-100% - 56px));
  min-height: 50px;
  z-index: 96;
  padding: 10px;
  background: var(--color-normal);
  color: var(--text-color-normal-fon);
  transition: 0.5s;
}
.alert.alert-general{
  z-index: 101;
  top: 0;
  width: calc(100% - 20px);
  transform: translateY(-100%);
  left: 0;
}
.alert.show{
  box-shadow:  0 10px 20px rgba(0, 0, 0, 0.5);
  transform: none;
}
.alert.alert-info,
.alert.alert-information{
  height: 60%;
  background: #555;
  border: 2px solid #751212;
  color: #fff;
}
.alert .div-alert-container{
  width: 100%;
  height: 100%;
  position: relative;
}
