.alert-messageDialog input{
  width: 50%;
  background: var(--content-color);
  border: 2px solid var(--head-color);
  color: var(--text-color);
  padding: 8px 20px;
  border-radius: 5px;
  outline: none;
}

.alert button.close{
  font-weight: bold;
  position: absolute;
  right: 10px;
  top:50%;
  width: 20px;
  height: 20px;
  transform: translateY(-50%);
  border-radius: 50%;
  border: solid 1px #a00;
  background: #f00;
  color: #fff;
}
.alert button.close:hover,
.alert.alert-info button.close:hover,
.alert.alert-information button.close:hover{
  color: #000;
  background: #c00;
}
.alert.alert-info button,
.alert.alert-information button{
  /* font-weight: bold; */
  position: absolute;
  right: 10px;
  top:10px;
  width: 25px;
  height: 25px;
  transform: translateY(-50%);
  border-radius:4px;
  border: solid 1px #a00;
  background: #f00;
  font-size: 20px;
  color: #fff;
}
.div-alert-btnConteiner{
  margin-top: 12px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.div-alert-btnConteiner button{
  border: none;
  background: var(--content-color);
  filter: brightness(var(--btn));
  padding: 8px 20px;
  border-radius: 5px;
  margin: 10px;
}
.div-alert-btnConteiner button:hover{
  box-shadow: inset 0 0 10px #000;
}
