@import "./slider.color.css";

.slider-box{
  width: 100%;
  height: 100%;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.slider-box .slider{
  height: 30px;
  width: calc(100% - 10px);
  display: flex;
  align-items: center;
}
.slider-box .slider input[type="range"]{
  height: 10px;
  width: 100%;
  outline: none;
  border-radius: 25px;
}
.slider input::-moz-range-progress,
.slider input::-webkit-slider-progress,
.slider input::-ms-progress{
  height: 10px;
  border-radius: 25px;
}
.slider input::-moz-range-thumb,
.slider input::-webkit-slider-thumb,
.slider input::-ms-thumb{
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
}
.slider-box .name{
  padding-top: 10px;
  font-size: 20px;
  width: 100%
}
.slider-box .value {
  font-size: 30px;
  font-weight: 600;
  font-family: sans-serif;
  width: 100%;
  text-align: center;
}
.delete-box{
  position: absolute;
  top:0;
  right: 0;
  width: 100%;
  height: 15px;
}
