.addElementBody{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.addElementBody .box{
  width: 300px;
  border-bottom:20px solid var(--color-darken);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.addElementBody .box h2{
  color: var(--text-color-darken-fon);
  background: var(--color-darken);
  padding: 10px 10px;
  font-size: 20px;
  width: calc(100% - 20px);
  font-weight: 700;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.addElementBody .box ul{
  position: relative;
  transition: 0.5s;
  /* background: #fff; */
}
.addElementBody .box ul li{
  list-style: none;
  position: relative;
  padding: 10px;
  width: calc(100% - 20px);
  background: #fff;
  box-shadow: 0 5px 25px rgba(0,0,0,0.1);
  transition: transform 0.5s;
}
.addElementBody .box ul li > input{
  padding: 5px;
  margin-bottom: 5px;
  width: calc(100% - 10px);
}
.addElementBody .box ul:hover li{
  opacity: 0.5;
}
.addElementBody .box ul li:hover{
  transform: scale(1.1);
  z-index: 100;
  background: var(--active-v1-color);
  box-shadow: 0 5px 25px rgba(0,0,0,.2);
  color: #fff;
  opacity: 1;
}
.addElementBody .box ul li.noAnim:hover{
  transform:none;
  background: #fff;
}
.addElementBody .box ul li span{
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  background: #25bcff;
  color: #fff;
  display: inline-block;
  border-radius: 50%;
  margin-right: 10px;
  font-size: 12px;
  font-weight: 600;
  transform: translateY(-2px);
}
.addElementBody .box ul li:hover span{
  background: #fff;
  color: #25bcff;
}
.addElementBody .box ul li button{
  width: 100%;
  border: none;
  height: 30px;
  background: #888;
  color:#fff;
  border-radius: 10px;
}
.addElementBody .box ul li button:hover{
  box-shadow: inset 0 0 10px #000;
}
